import React from 'react';
import logo from "../assets/images/logo.png"; // Make sure to adjust the path based on your file structure.

const Header = () => {
  return (
    <header>
    <a href="/">
      <img src={logo} alt="Capac Urcu Logo" style={{ width: "150px" }} />
    </a>
    <h1>Welcome to Capac Urcu</h1>
    <nav>
      <ul>
        <li>
          <a href="/">Home</a>
        </li>
        <li>
          <a href="/about">About Us</a>
        </li>
        {/* <li>
          <a href="#gallery">Gallery</a>
        </li> */}
        <li>
          <a href="/contact">Contact</a>
        </li>
      </ul>
    </nav>
  </header>
  );
};

export default Header;