import React from 'react';
import logo from "../assets/images/logo.png"; // Make sure to adjust the path based on your file structure.

const Footer = () => {
  return (
      <footer>
        <img src={logo} alt="Capac Urcu Logo" style={{ width: "100px" }} />
        <p>&copy; 2024 Capac Urcu. All rights reserved.</p>
      </footer>
  );
};

export default Footer;