import React from "react";
import "../styles/styles.css"; // Import your stylesheet if you need it

const Contact = () => {
  return (
    <main>
        <section id="contact">
            <h1>Contact Us</h1>
            <p>If you have any questions, inquiries, or would like to know more about Capac Urcu, feel free to reach out to us.!</p>
            
            <form action="https://formspree.io/f/meojbaed" method="POST">
                <label for="name">Your Name:</label><br></br>
                <input type="text" id="name" name="name" required></input><br></br>
                <br></br>
                <label for="email">Your Email:</label><br></br>
                <input type="email" id="email" name="email" required></input><br></br>
                <br></br>
                <label for="message">Your Message:</label><br></br>
                <textarea id="message" name="message" rows="5" required></textarea><br></br>
                <br></br>
                <input type="hidden" name="_subject" value="New message from Capac Urcu Website"></input><br></br>
            
                <button type="submit">Send Message</button>
            </form>            
            <p>You can also reach us via email at: <a href="mailto:info@capacurcu.com">info@capacurcu.com</a></p>
        </section>
    </main>
  );
};

export default Contact;